* {
    box-sizing: border-box;
}
body {
    margin: 0;
    padding: 0;
    background-color: #eff3f7;
}

html,
body,
.container {
    height: 100%;
    display: flex;
    justify-content: center;
}

.loader {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center; /* Loading 글자를 정가운데로 가게 만듬 */

}

.movies {
    display:flex;/* 세로로 정렬된 영화를 가로로 쫙붙여줌 */
    justify-content: space-between;
    align-items: flex-start;
    flex-wrap: wrap;
    padding: 50px;
    padding-top: 70px;
    width: 80%;

}

.movies .movie {
    width: 40%;
    background-color:white;
    display: flex;
    align-items: flex-start;
    justify-content: space-between;

    font-weight: 300;
    padding: 20px;
    margin-top: 40px;
    margin-bottom: 80px;
    border-radius: 50px 50px 100px 50px;
    color: dimgrey;
    box-shadow: 10px 10px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px gold, 0 8px 16px -8px , 0 -6px 16px -6px red;
}

.movies .movie__data {
    width: 50%;
}

.movie img {
    position: relative;
    top: -50px;
    max-width: 180px;
    width: 100%;
    margin-right: 30px;
    box-shadow: 0 30px 60px -12px rgba(50, 50, 93, 0.25),
    0 18px 36px -18px rgba(0, 0, 0, 0.3), 0 -12px 36px -8px rgba(0, 0, 0, 0.025);
}

.movie .movie__title,
.movie .movie__year {
    margin: 0;
    font-weight: 300;
}
.movie .movie__title {
    margin-bottom: 5px;
    font-size: 24px;
}
.movie .movie__genres {
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;
    flex-wrap: wrap;
    margin: 5px 0px ;
}

.movie__genres li,
.movie .movie__year {
    margin-right: 10px;
}

